<template>
  <v-container id="regular-forms" fluid tag="section">
    <base-material-card color="success" inline title="企业编辑" class="px-4 py-6" style>
      <v-form class="pt-1" v-model="valid" :lazy-validation="lazy">
        <v-row align="center">
          <v-col cols="12" sm="6">
            <v-text-field
              :rules="nameRules"
              required
              label="公司名称(*必填)"
              v-model="companyObj.companyName"
              outlined
              class="ml-2"
            />
          </v-col>
          <v-col cols="12" sm="6">
            <v-text-field
              :rules="nameRules"
              required
              label="公司标识(*必填)"
              v-model="companyObj.customerUniqueCode"
              outlined
              class="ml-2"
            />
          </v-col>
        </v-row>
        <v-row align="center">
          <v-col cols="12" sm="6">
            <v-text-field
              :rules="nameRules"
              required
              label="客户数量(*必填)"
              v-model="companyObj.customerNumber"
              outlined
              class="ml-2"
            />
          </v-col>
        </v-row>
        <v-row align="center">
          <v-col cols="12" sm="6">
            <v-text-field
              label="cropId(选填)"
              v-model="companyObj.cropId"
              outlined
              class="ml-2"
            />
          </v-col>
          <v-col cols="12" sm="6">
            <v-text-field
              label="securte(选填)"
              v-model="companyObj.securte"
              outlined
              class="ml-2"
            />
          </v-col>
        </v-row>
        <v-row align="center">
          <v-col cols="12">
            <v-textarea
              outlined
              class="ml-2"
              name="input-7-4"
              label="公司信息(选填)"
              v-model="companyObj.companyContent"
            />
          </v-col>
        </v-row>
      </v-form>
      <v-row align="center">
        <v-spacer></v-spacer>
        <v-btn color="blue darken-1" text @click="doCancel()">关闭</v-btn>

        <v-btn color="blue darken-1" :disabled="!valid" text @click="doSave()">保存</v-btn>
      </v-row>
    </base-material-card>
  </v-container>
</template>

<script>
  export default {
    created() {
        let getData = {id:this.$route.query.companyId};
        this.$http.get("/user/api/v1/company/selectCompanyById",getData).then(res => {
        this.companyObj = res.data;
        })
    },

    name: 'editCompanyInfo',

    data: () => ({

    companyObj:{
        id:'',
        companyName:'',
        cropId:'',
        securte:'',
        customerNumber:'',
        companyContent:'',
        customerUniqueCode:'',
        purchaseDays:'',
        version:'',
    },

    lazy: false,
    valid: true,
     //表单验证
    nameRules: [v => !!v || "该选项为必填字段"]
    }),

    methods: {
     doSave(){
       let postData={id:this.companyObj.id,companyName:this.companyObj.companyName,
       cropId:this.companyObj.cropId,
       securte:this.companyObj.securte,
       customerNumber:this.companyObj.customerNumber,
       companyContent:this.companyObj.companyContent,
       customerUniqueCode:this.companyObj.customerUniqueCode,
       purchaseDays:this.companyObj.purchaseDays};
        this.$http
        .post("/user/api/v1/company/updateCompany", postData)
        .then(response => {
          if (response.success) {
            response.msg = "企业更新成功";
             this.$store.commit("SET_SNACK", response);
             this.$router.push({path:'companyManage'});
          }else{
             this.$store.commit("SET_SNACK", response);
          }
         
        })
        .catch(error => {
          console.log(error);
        });
     },
      doCancel(){
       this.$router.push({path:'companyManage'});
     }
    },
  }
</script>
